// Mixins

@mixin flexthing {
  display: flex;
  justify-content: center;
  align-items: center;
}

@mixin forfonts {
  color: rgba(255, 255, 255, 0.548);
  font-family: "Big Shoulders Stencil Text", cursive;
  letter-spacing: 0.3rem;
  text-shadow: 0 0 1px rgba(255, 255, 255, 0.596), 0 0 3px #fff,
    0 0 5px rgba(255, 255, 255, 0.52), 0 0 2px #49ff18, 0 0 3px #49ff18,
    0 0 40px #4aff187e;
  font-weight: lighter;
}
//mixin for the loading part
@mixin sterne {
  background: linear-gradient(0.15turn, #10a6c081, #10801652, #ffffff);
  position: absolute;
  border-radius: ((random(50) + 100 + px)) ((random(20) + 100 + px)) 0 0;
  border-radius: 50%;

  box-shadow: 0 0 6px 2px rgb(53, 136, 15),
    -4px -0px 20px -1px rgba(255, 255, 255, 0.431);
}

@mixin add-move-animation($duration) {
  animation-name: move;
  animation-duration: $duration;
  animation-iteration-count: infinite;
  animation-direction: normal;
  animation-timing-function: linear;
}
@mixin frame_for_list_items {
  box-shadow: 0px 0px 18px 3px rgba(135, 243, 150, 0.198);
}

//keyframes for the loading part
@keyframes move {
  0% {
    transform: rotate(00deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

//to rotate the planet menu
@keyframes rotateit {
  to {
    transform: rotate(360deg);
  }
}
